@import url("https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap");

.App {
  text-align: center;
  font-family: "Fredoka One", cursive;
}

.headline {
  font-size: 10vw;
  margin: 10px;
}
.intro {
  margin: 30px;
  padding: 50px;
}
.profile-img {
  border: solid black 5px;
  margin: 30px;
  transition: transform 3s ease-in-out;
}
.profile-img:hover {
  transform: scale(1.3);
}
.second-title {
  margin: 50px;
}

.project-img {
  width: 600px;
  height: 400px;
  border: solid black 5px;
  transition: transform 3s ease-in-out;
}
.project-img:hover {
  transform: scale(1.1);
}

.project-img-lp {
  width: 350px;
  height: 250px;
  border: solid black 5px;
  transition: transform 3s ease-in-out;
}
.project-img-lp:hover {
  transform: scale(1.3);
}

p {
  margin: 50px;
}
.btn {
  margin: 30px;
  padding: 30px;
  width: 200px;
  border: solid black 3px;
}
.exp-img {
  width: 100px;
  height: 100px;
}
.soc-ico {
  width: 50px;
  height: 50px;
}

.footer a {
  font-size: 2.5rem;
  margin: 10px;
}
/*.footer a:hover {
  transition: 0.7s;
}*/
.footer .soc-ico:hover {
  animation: footer 0.7s ease;
}

@keyframes footer {
  100% {
    transform: rotate(360deg);
  }
}
